.interior-items__link {
  display: block;
  width: auto;
  position: relative;
  font-weight: bold;
  font-size: 19px;
  line-height: 1.26;
  color: $color-white-1;
  text-decoration: none;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 30px;

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 10px;
    right: 10px;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.interior-items__link--interior {
  background-color: $color-green-1;
  padding-right: 60px;

  &::after {
    width: 40px;
    height: 62px;
    background-image: url("../img/icon-interior.svg");
  }
}

.interior-items__link--toy {
  background-color: $color-green-2;
  padding-right: 80px;

  &::after {
    width: 60px;
    height: 60px;
    background-image: url("../img/icon-toy.svg");
  }
}

@media (min-width: $tablet-width) {
  .interior-items {
    display: flex;
    margin-left: 52px;
    margin-right: 52px;
  }

  .interior-items__link-block {
    flex-grow: 1;
  }

  .interior-items__link {
    font-size: 20px;
    line-height: 1.5;
    padding-top: 40px;
    padding-bottom: 60px;
    padding-left: 40px;

    &::after {
      top: 25px;
      right: 20px;
    }
  }

  .interior-items__link--interior {
    padding-right: 70px;
  }

  .interior-items__link--toy {
    padding-right: 90px;
  }
}
